import store from "@/store"
function HTTP(obj, config) {

	let defaultConfig = {
		isRes: false,
		loading: false
	}

	config = { ...defaultConfig,
		...config
	}


	// loading,mask
	config.loading && uni.showLoading({
		title: '',
		mask: true
	});

	return new Promise((resolve, reject) => {

		let options = {
			url: "",
			method: "GET",
			data: {},
			dataType: "json",
			header: {
				'content-type': 'application/x-www-form-urlencoded',
				'time': new Date().getTime(),
				'version': '1.0.0',
				'platform': JSON.stringify(uni.getSystemInfoSync()),
				'token': store.state.token
			},
			success: (res) => {
				console.log("HTTP",res)
				uni.hideLoading();
				// 200
				if (res.statusCode == 200) {
					let data = res.data;
					//
					// if (data.code == "001") {
					// 	uni.showToast({
					// 		title: '',
					// 		icon: 'none',
					// 		duration: 2000
					// 	})
					// 	setTimeout(()=>{
					// 		store.dispatch("reLogin");
					// 	},2000)
					// 	return;
					// }
					// else {
					if (data.code == 0 || data.code == 8) {
						resolve(data)
					}else if(data.code == 5 ){
						store.commit('SET_TOKEN','')
					}else{
						uni.showToast({
							title: data.msg,
							icon: "none",
							duration: 2000
						})
						reject(data.msg);
					}
					//}
				} else {
					reject(res.data.msg)
					//reject("HTTP:");
				}
			},
			fail: (err) => {
				uni.hideLoading();
				uni.showToast({
					title: "!",
					icon: "none",
				})
				reject("!");
			},
			complete: () => {}
		}

		options = { ...options,
			...obj
		};
		
		const OPENID = uni.getStorageSync("openId");
		if (OPENID) options["header"]["openId"] = OPENID;

		if (options.url && options.method) {
			uni.request(options);
		} else {
			uni.showToast({
				title: 'HTTP',
				icon: "none",
				duration: 2000
			})
		}
	})

}



export default {
	GET(url, data = {}, config) {
		return HTTP({
			url,
			data,
			method: "GET"
		}, config);
	},
	POST(url, data = {}, config) {
		return HTTP({
			url,
			data,
			method: "POST"
		}, config);
	}
}